<template>
    <div>
        <div class="blog-content">
            <div class="container">
                <div class="row">
                    <div class="col-lg-10 offset-lg-1">
                        <div class="blog-header">
                            <ol class="breadcrumb">
                                <li>当前位置：</li>
                                <li class="breadcrumb-item"><router-link to="/Blog">Blog</router-link></li>
                                <li class="breadcrumb-item text-truncate" style="max-width:189px">{{blogContent.title}}</li>
                            </ol>
                            <h2>{{blogContent.title}}</h2>
                            <p>
                                标签：
                                <span v-for="(item, index) in blogContent.tag" :key="index">{{item}}</span>
                            </p>
                            <hr>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-lg-7 offset-lg-1">
                        <div class="blog-container">
                            
                        </div>
                        <hr>
                        <p>创建日期：{{blogContent.created_dt}}</p>
                        <p>更新日期：{{blogContent.updated_dt}}</p>
                    </div>
                    <div class="col-lg-3">
                        <div class="blog-nav d-lg-block d-none" v-if="blogContent.nav">
                            此处为博客导航
                        </div>
                        <div class="blog-direction d-lg-block d-none">
                            <div>
                                上一篇：
                                <a href="#">A</a>
                            </div>
                            <div>
                                下一篇：
                                <a href="#">A</a>
                            </div>
                        </div>
                        <ul class="blog-related">
                            <li>相关博客</li>
                            <li><a href="#">博客1</a></li>
                            <li><a href="#">博客2</a></li>
                            <li><a href="#">博客3</a></li>
                            <li><a href="#">博客4</a></li>
                            <li><a href="#">博客5</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            blogContent: {}
        }
    },
    mounted() {
        this.$axios.get("/static/mock/index.json").then(res => {
            res.data.blog.forEach(item => {
                if(item.blogId == this.$route.params.blogId){
                    this.blogContent = item
                }
            })
        })
    }
}
</script>

<style scoped>
    .blog-content {
        margin: 2rem;
    }
    .blog-header {
        margin-bottom: 1rem;
    }
    .blog-header h2 {
        margin-bottom: 1rem;
    }
    p {
        margin-bottom: 0;
    }
    .blog-header span {
        display: inline;
        background: #000;
        color: #fff;
        padding: 5px;
        cursor: pointer;
        margin-bottom: 10px;
        margin-right: 10px;
    }
    .blog-header span:last-of-type {
        margin-right: 0;
    }
    hr {
        opacity: 1;
    }
    .blog-nav {
        background: #f1f1f1;
        padding: 1rem;
        margin-bottom: 1rem;
    }
    .blog-direction {
        margin-bottom: 1rem;
    }
    .blog-direction div {
        background: #000;
        color: #fff;
        padding: 0.5rem 1rem;
        margin-bottom: 1rem;
    }
    .blog-direction div:last-of-type {
        margin-bottom: 0;
    }
    .blog-direction a {
        color: #fff;
        text-decoration: underline #fff;
    }
    .blog-direction a:hover {
        text-decoration: underline dotted #fff;
    }
    .blog-related {
        list-style: none;
        padding: 0;
        border: 1px solid #f1f1f1;
    }
    .blog-related li a {
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        max-width: 100%;
    }
    .blog-related li {
        border-bottom: 1px solid #f1f1f1;
        padding: 0.5rem 1rem;
    }
    .blog-related li:first-of-type {
        background: #f1f1f1;
    }
    .blog-related li:last-of-type {
        border-bottom: none;
    }
    
</style>


